import {
    Channel,
    Client,
    DMChannel, GuildMember,
    Message,
    MessageEmbed,
    NewsChannel, PartialDMChannel, Role,
    TextChannel,
    User
} from "discord.js";

export interface IWhatAmICallback {
    (param: WhatAmICallbackParam): void
}

export type WhatAmI = "member" | "channel" | "role" | "message" | "not found"

export interface WhatAmICallbackParam {
    entity: GuildMember | PartialDMChannel | Channel | Message | Role | undefined,
    whatAmI?: WhatAmI
}

export interface ChannelPerm {
    Id: number,
    name: string,
    type: "text" | "voice",
    aliases: string[]
}

/**
 *
 * @param {string} body Description of message embed.
 * @param {string} title Title of message embed. Default: "Info:"
 * @param {*} client The client that instantiated this (visible in title)
 * @param {*} perpetrator Discord.User that used the command (tagged in footer)
 * @returns
 */
export function embed(
    body: any,
    title: string = "Info:",
    perpetrator: User | null = null,
    client: Client | null = null
) {
    const date = new Date(Date.now());

    return new MessageEmbed()
        .setColor("#a039a0")
        .setAuthor(
            `Powered by ${client?.user?.username ?? "Kifo Clankerâ„¢"}`,
            undefined,
            `https://kifopl.github.io/kifo-clanker/`
        )
        .setTitle(title)
        .setDescription(body)
        .setFooter(
            `${
                perpetrator == null
                    ? "N"
                    : `Command issued by ${perpetrator.tag}, n`
            }pm by KifoPL, sent at: ${date.toUTCString()}.`
        );
};

/**
 * Trims mentions to Ids. For instance, "`<@KifoPL>`" returns "`KifoPL`", "`<@&123123123>`" returns "`123123123`", "`42069`" returns "`42069`".
 * @param {string} mention the mention to trim
 * @returns raw Id as a string
 */
export function mentionTrim(mention: string) {
    return mention.replace(mentionRegEx(), "$1");
};

/**
 * Returns Id of emoji, really handy for handling reactions, etc.
 * @param {*} emojiIdentifier The <a:name:id>, <:name:id>, a:name:id or name:id emoji identifier string of an emoji
 * @returns emoji Id
 */
export function emojiTrim(emojiIdentifier: string) {
    return emojiIdentifier.replace(/[^\d]/g, "");
};

/**
 *
 * @param {number} number the place
 * @returns number with place abbreviation: for `1` returns `1st`, `2` - `2nd`, etc...
 */
export function place(number: number) {
    if (number % 10 == 1 && number % 100 != 11) return `${number}st`;
    if (number % 10 == 2 && number % 100 != 12) return `${number}nd`;
    if (number % 10 == 3 && number % 100 != 13) return `${number}rd`;
    else return `${number}th`;
};


/**
 *
 * @param {*} message The message sent
 * @param {*} whatIsThis The argument in question (either mention or Id)
 * @param {*} allowWords True if "me" results in member and "here" in channel (case in-sensitive), false otherwise.
 * @param {*} callback { entity: `GuildMember` or `GuildChannel` or `GuildRole` or `GuildMessage` or `undefined`, whatAmI: "member" or "channel" or "role" or "message" or "not found" (that's because if the function breaks, it will return `undefined`, + it's easier to do if statements)}
 * @returns
 */
export async function whatAmIFunc(
    message: Message,
    whatIsThis: string,
    allowWords: boolean = false,
    callback: IWhatAmICallback
) {
    let entity = undefined;
    let whatami: WhatAmI = "not found";

    whatIsThis = mentionTrim(whatIsThis);

    if (whatIsThis.toUpperCase() == "ME" && allowWords) {
        entity = message.member!;
        whatami = "member";
        callback({entity, whatAmI: "member"});
        return;
    } else if (whatIsThis.toUpperCase() == "HERE" && allowWords) {
        entity = message.channel;
        whatami = "channel";
        callback({entity: entity, whatAmI: whatami});
    } else {
        if (!message.guild?.members.resolve(whatIsThis)) {
            if (!message.guild?.roles.resolve(whatIsThis)) {
                if (!message.guild?.channels.resolve(whatIsThis)) {
                    await message.guild?.channels.cache
                        .filter((ch) => ch.isText())
                        .each(async (ch) => {
                            await (ch as TextChannel | NewsChannel | DMChannel).messages
                                .fetch(whatIsThis)
                                .then((msg) => {
                                    entity = msg;
                                    whatami = "message";
                                    callback({
                                        entity: entity,
                                        whatAmI: whatami,
                                    });
                                    return;
                                })
                                .catch(() => {
                                });
                        });
                    if (entity != undefined) {
                        whatami = "message";
                    } else {
                        whatami = "not found";
                        callback({entity: entity, whatAmI: whatami});
                        return;
                    }
                } else {
                    whatami = "channel";
                    entity = message.guild.channels.resolve(whatIsThis)!;
                    callback({entity: entity, whatAmI: whatami});
                    return;
                }
            } else {
                whatami = "role";
                entity = message.guild.roles.resolve(whatIsThis)!;
                callback({entity: entity, whatAmI: whatami});
                return;
            }
        } else {
            whatami = "member";
            entity = message.guild.members.cache.find(
                (member) => member.id == whatIsThis
            )!;
            callback({entity: entity, whatAmI: whatami});
            return;
        }
    }
};

/**
 * @returns regex fitting discord mentions, like <@!KifoPL#3358>, <@289119054130839552>, etc. (works for members, roles, channels
 */
export function mentionRegEx(): RegExp {
    return /^<[@#&!](.+)>/;
}

