/**
 *
 * @returns RegEx fitting all URLs starting with http:// or https://
 */
export function urlRegex(): RegExp {
    return /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()!@:%_\+.~#?&\/\/=]*)/g;
};

